import { css, keyframes } from "styled-components";

const fadeInKeyframes = keyframes`
from{
  filter:blur(1); 
  opacity:0; 
}
to{
  filter:blur(0);
  opacity:1;
}
`;
const fadeOutKeyframes = keyframes`
from{
  filter:blur(0); 
  opacity:1; 
}
to{
  filter:blur(1);
  opacity:0;
}
`;

const FadeOut = ({ time = "1s", type = "ease-out" } = {}) => css`
  animation: ${fadeOutKeyframes} ${time} ${type};
`;

export const FadeIn = ({ time = "1s", type = "ease-in" } = {}) => css`
  animation: ${fadeInKeyframes} ${time} ${type};
`;
