import { createAsyncThunk, createSlice, createSelector } from "@reduxjs/toolkit";
import { getAnimeByQuery } from "../../api/animeAPI";

const initialState = {
  animes: {},
  loading: false,
};

export const fetchAnimeByQuery = createAsyncThunk(
  "anime/fetchAnimeByQuery",
  async (query) => {
    const response = await getAnimeByQuery(query);
    // The value we return becomes the `fulfilled` action payload
    console.log("response", response);
    return response;
  }
);

export const animeSlice = createSlice({
  name: "animes",
  initialState,
  reducers: {
    getAnimes: (state) => {
      state.anime = { name: "Boku no hero", episodes: 120 };
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchAnimeByQuery.pending, (state) => {
        state.loading = true;
        console.log("pending", state.loading);
      })
      .addCase(fetchAnimeByQuery.fulfilled, (state, action) => {
        state.animes = action.payload.results;
        state.loading = false;
      });
  },
});

export const { getAnimes } = animeSlice.actions;

export const selectAnimes = (state) => state.animes.animes;
export const selectLoading = (state) => state.animes.loading;
// export const selectAnimeById = (state) => (id) =>
//   state.anime.anime.find((anime) => {
//     console.log("id", id, anime.mal_id);
//     return anime.mal_id === id ? anime : null;
//   });
export const selectAnimeById = createSelector(
  selectAnimes,
  (animes) => (id) => animes.filter((anime) => anime.id === id)
);

export default animeSlice.reducer;
