import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import CardsContainerCarosuel from "../../components/cards/CardsContainerCarosuel";
import Card from "../../components/cards/Card";
import {
  selectAnimes,
  selectLoading,
  fetchAnimeByQuery,
} from "../../reducers/animes/index";
import Loader from "../../components/loader/Loader";

const useAnimesData = () => {
  const isLoading = useSelector(selectLoading);
  const dispatch = useDispatch();
  const animes = useSelector(selectAnimes);
  useEffect(async () => {
    dispatch(fetchAnimeByQuery("One Piece"));
  }, []);
  return { animes, isLoading };
};

const Home = () => {
  const { animes, isLoading } = useAnimesData();
  return (
    <>
      <div className="container">
        <div className="row">
          <div className="col text-center">
            {isLoading ? (
              <Loader />
            ) : (
              <CardsContainerCarosuel>
                {animes.length > 1 ? (
                  animes.map((result) => <Card key={result.mal_id} anime={result} />)
                ) : (
                  <span>No result</span>
                )}
              </CardsContainerCarosuel>
            )}
          </div>
        </div>
      </div>
    </>
  );
};

export default Home;
