import React from "react";
import { useParams } from "react-router-dom";
import { useSelector } from "react-redux";
import { selectAnimeById } from "../../reducers/animes/index";
import Loader from "../../components/loader/Loader";

const AnimePage = () => {
  const { id } = useParams();
  // console.log("id", id);
  const anime = useSelector(selectAnimeById(id));

  return (
    <>
      {anime ? (
        <Loader />
      ) : (
        <div className="container">
          <div className="row">
            <div className="col-6">
              <img src="" alt="" />
            </div>
            <div className="col-6">
              <span>{anime.title}</span>
              <span>{anime.rate}</span>
              <span>{anime.score}</span>
              <span>{anime.start_date}</span>
              <p>{anime.sipnosis}</p>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default AnimePage;
